const directionsArrows = document.querySelectorAll(".arrows__arrow");
console.log("start");

const pointerOverEvent = (e) => {
  console.log("jestem hoover 1");
  e.target.style.fill = "#86BAB3";
  // e.target.classList.toggle("active");
  e.target.style.opacity = 0.6;
};

const mouseLeaveEvent = (e) => {
  console.log("jestem hoover 2");

  setTimeout(() => {
    e.target.style.fill = "#d9f4f1";
    e.target.style.opacity = 0.1;
    // e.target.classList.toggle("active");
  }, "1000");
};

const addHoverListeners = () => {
  console.log("start dodawania hover贸w");
  directionsArrows.forEach((arrow) => {
    arrow.addEventListener("pointerover", pointerOverEvent);
  });

  directionsArrows.forEach((arrow) => {
    arrow.addEventListener("mouseleave", mouseLeaveEvent);
  });
  console.log("koniec dodawania hover贸w");
};

const removeHoverListeners = () => {
  console.log("start usuwania hover贸w");

  directionsArrows.forEach((arrow) => {
    arrow.removeEventListener("pointerover", pointerOverEvent);
    arrow.removeEventListener("mouseleave", mouseLeaveEvent);
  });

  console.log("koniec usuwania hover贸w");
};

addHoverListeners();

directionsArrows.forEach((arrow) => {
  arrow.addEventListener("click", () => {
    console.log("zostalem klikniety");
    removeHoverListeners();
    directionsArrows.forEach((el) => (el.style.opacity = 0.1));
    arrow.style.opacity = 1;
    arrow.style.fill = "#d9f4f1";

    // arrow.classList.toggle("active");
    // document.querySelector(.)
  });
});

